import { Component, OnInit } from '@angular/core';
import {NewsStore} from "../../store/news.store";
import {LessonStore} from "../../store/lesson.store";
import {Router} from "@angular/router";
import {NavController} from "@ionic/angular";
import {LessonService} from "../../service/lesson.service";

@Component({
  selector: 'app-lesson',
  templateUrl: './lesson.page.html',
  styleUrls: ['./lesson.page.scss'],
})
export class LessonPage implements OnInit {
  pageType: number = 0;
  correctAnswers: number = 0;
  questionPage: number = 0;
  page: number = 0;
  constructor(private lessonService: LessonService, public lessonStore: LessonStore, private router: Router, private navCtrl: NavController) {
  }

  ngOnInit() {
  }

  incrementPage(){
    this.page++;
  }
  decrementPage(){
    this.page--;
  }

  async answerQuestion(answer: string){
    if(answer===this.lessonStore.selectedLesson.questions[this.questionPage].correctAnswer){
      this.correctAnswers++;
    }
    if(this.questionPage===this.lessonStore.selectedLesson.questions.length-1){
      this.pageType = 2;
      if(this.correctAnswers===this.lessonStore.selectedLesson.questions.length){
        await this.lessonService.finishLesson(true);
      }else {
        await this.lessonService.finishLesson(false);
      }
    }else{
      this.questionPage ++;
    }
  }
  gotoQuestions(){
    // this.router.navigate(['/question']);
    this.pageType=1;

  }

  async goBack(){
    await this.navCtrl.pop();
  }

}
