import {Injectable} from '@angular/core';
import * as Parse from 'parse';


@Injectable({
    providedIn: 'root'
})

export class ParseHelper {
    constructor() {
    }

    static arrayToJSON(parseArray: Parse.Object[]): any[]{
      const jsonArray: any[] = [];
      for(const parseObject of parseArray){
        jsonArray.push(parseObject.toJSON());
      }
      return jsonArray;
    }
    static getParseObjectWithoutData(className: string, id: string): Parse.Object {
    try {
      const ObjectTable = Parse.Object.extend(className);
      const object = ObjectTable.createWithoutData(id);
      return object;
    } catch (e) {
      console.log('Error: ', e);
      return null;
    }


  }
}

